// IDCard.js
import React from "react";
import { BASE_URL } from "./constant";
import BannerImage from "./images/Frame.jpg";

const IDCard = ({ days, name, imageUrl }) => {
  return (
    <>
      <div className="image-frame">
        <img src={BannerImage} className="img-temp" />
        <div className="user-image">
          <img src={`${BASE_URL}/${imageUrl}`} className="img-user" />
        </div>
        <div className="abs-name">
          <p className="username">{name}</p>
          <p className="tickeDays">{days} Day Pass</p>
        </div>
      </div>
    </>
  );
};

export default IDCard;
