import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";
import axios from "axios";
import { BASE_URL, axiosConfig } from "./constant";
import { useNavigate } from "react-router-dom";
import Header from "./componants/header";
import Footer from "./componants/footer";
import Banner from "./componants/Banner";
const Payment = ({}) => {
  const location = useLocation();
  const responseData = location.state ? location.state.responseData : null;
  const [paymentResponse, setPaymentResponse] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const key = "KIRLHAN0WI";
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    unique_id: 0,
    name: "",
    phone: "",
    amount: 0,
  });
  const [users, setUsers] = useState([]);

  // Update form data when the component mounts
  useEffect(() => {
    if (responseData && responseData.users && responseData.users.length > 0) {
      // Set the users state with the array from the JSON
      setUsers(responseData.users);
    }
    if (responseData) {
      setUsers(responseData.users);
      setFormData({
        unique_id: responseData.users[0].registrationId,
        name: responseData.users[0].name,
        phone: responseData.users[0].phone,
        amount: responseData.paymentAmount,
      });
    }
  }, [responseData]);

  const handlePayment = async (e) => {
    e.preventDefault();
    try {
      setIsLoading(true);
      const SendData = {
        unique_id: formData.unique_id,
        name: formData.name,
        phone: formData.phone,
        amount: formData.amount,
        email: "myofai@gmail.com",
        txnid: "T" + Date.now(),
        furl: `${BASE_URL}/fail`,
        surl: `${BASE_URL}/success`,
        productinfo: "Event Registration",
      };
      const response = await axios.post(`${BASE_URL}/api/initiate_payment`, {
        SendData,
        axiosConfig,
      });
      if (response.data.data) {
        setIsLoading(false);
      }

      if (response.data.data) {
        const easebuzzCheckout = new window.EasebuzzCheckout(key, "prod");

        const options = {
          access_key: response.data.data,
          onResponse: async (response) => {
            await updatePaymentStatus(response.status);
            setPaymentResponse(response);
            if (response.status === "success") {
              navigate("/success", {
                state: { responseData: users },
              });
            }
          },
          theme: "#123456", // Replace with your desired color hex
        };

        // Execute Easebuzz Checkout
        easebuzzCheckout.initiatePayment(options);
      } else {
        console.error("Access key not available. Payment initiation failed.");
      }
    } catch (error) {
      console.error("Error processing payment:", error);
    }
  };

  const updatePaymentStatus = async (status) => {
    const resultArray = users.map((user) => ({
      id: user.registrationId,
      status: status,
    }));
    console.log(resultArray);
    try {
      await axios.post(
        `${BASE_URL}/api/update_payment_status`,
        resultArray,
        axiosConfig
      );
    } catch (error) {
      console.error("Error updating payment status:", error);
    }
  };

  return (
    <>
      <Header />
      <Banner />
      <section className="payment-page">
        {isLoading && (
          <div className="loading-overlay">
            <div className="loading-spinner"></div>
          </div>
        )}
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <h3 className="ova_title2 mb-5">
                Verify and completete the payment RS:
                {formData && formData.amount}
              </h3>
              <div className="registrationForm">
                <div className="table-responsive">
                  <table className="table table-bordered">
                    <thead className="table-header">
                      <tr>
                        <th>No</th>
                        <th>Name</th>
                        <th>Phone</th>
                      </tr>
                    </thead>
                    <tbody>
                      {users.map((user, index) => (
                        <tr key={index}>
                          <td> {index + 1}</td>
                          <td> {user.name}</td>
                          <td>{user.phone}</td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
                <div className="form-group row">
                  <div className="col-md-12">
                    <div className="btn-wrapper">
                      <button
                        type="submit"
                        onClick={handlePayment}
                        className="btn-submit"
                      >
                        Proceed to Pay
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              {paymentResponse && paymentResponse.status !== "success" && (
                <div className="mt-3 text-center alert alert-danger">
                  <h4>Payment failed or canceled. Please try again.</h4>
                </div>
              )}
            </div>
          </div>
        </div>
      </section>
      <Footer />
    </>
  );
};

export default Payment;
