import React, { useState, useEffect } from "react";
import axios from "axios";
import { BASE_URL, axiosConfig } from "./constant";
import { Link, useNavigate } from "react-router-dom";
import Header from "./componants/header";
import Footer from "./componants/footer";
import Banner from "./componants/Banner";
const Form = () => {
  const [totalAmount, setTotalAmount] = useState(0);
  const [users, setUsers] = useState([
    {
      name: "",
      phone: "",
      email: "",
      district: "",
      customDistrict: "",
      pincode: "",
      days: [],
    },
  ]);
  const [errors, setErrors] = useState({});
  const [registrationSuccess, setRegistrationSuccess] = useState(false);
  const [districts, setDistricts] = useState([]);
  const navigate = useNavigate();
  const [isButtonDisabled, setButtonDisabled] = useState(true);
  useEffect(() => {
    // Fetch districts when the component mounts
    fetchDistricts();
  }, []);

  const fetchDistricts = async () => {
    try {
      const response = await axios.get(`${BASE_URL}/api/districts`);
      const result = response.data;

      if (result.success) {
        setDistricts(result.districts);
      } else {
        console.error("Error fetching district data:", result.error);
      }
    } catch (error) {
      console.error("Error fetching district data:", error);
    }
  };

  const handleChange = (e, index) => {
    const { name, value } = e.target;
    const updatedUsers = [...users];

    // If the selected district is "Other", update the customDistrict field
    if (name === "district" && value === "Other") {
      updatedUsers[index].customDistrict = ""; // Clear the customDistrict field initially
    }

    updatedUsers[index][name] = value;
    setUsers(updatedUsers);
  };

  const handleCheckboxChange = (e, index) => {
    setButtonDisabled(false);
    const isChecked = e.target.checked;
    const checkboxValue = isChecked ? 118 : -118;

    // Update the total amount
    setTotalAmount((prevTotal) => prevTotal + checkboxValue);

    const { checked, name } = e.target;
    const updatedUsers = [...users];
    const user = updatedUsers[index];

    if (checked && !user.days.includes(name)) {
      user.days.push(name);
    } else if (!checked && user.days.includes(name)) {
      user.days = user.days.filter((day) => day !== name);
    }

    setUsers(updatedUsers);
  };

  const validateForm = () => {
    const updatedErrors = {};

    users.forEach((user, index) => {
      if (!user.name) {
        updatedErrors[`name${index}`] = "Name is required";
      }

      if (!user.phone) {
        updatedErrors[`phone${index}`] = "Phone number is required";
      } else if (!/^\d+$/.test(user.phone)) {
        updatedErrors[`phone${index}`] =
          "Phone number must contain only numbers";
      }

      if (!user.district) {
        updatedErrors[`district${index}`] = "District is required";
      }

      // Add more validation as needed for other fields

      // Reset errors for valid fields
      if (!updatedErrors[`name${index}`]) {
        delete updatedErrors[`name${index}`];
      }
      if (!updatedErrors[`phone${index}`]) {
        delete updatedErrors[`phone${index}`];
      }
      if (!updatedErrors[`district${index}`]) {
        delete updatedErrors[`district${index}`];
      }
    });

    setErrors(updatedErrors);

    return Object.keys(updatedErrors).length === 0;
  };

  const saveFormData = async () => {
    const finalData = { users: users, amount: totalAmount };
    try {
      const response = await axios.post(
        `${BASE_URL}/api/saveFormData`,
        finalData,
        axiosConfig
      );
      return response.data;
    } catch (error) {
      console.error("Error saving form data:", error);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (validateForm()) {
      try {
        const duplicateNumbers = await checkDuplicateMobileNumbers();

        if (duplicateNumbers.length > 0) {
          // Show an error message for duplicate mobile numbers
          setRegistrationSuccess(false);

          setErrors({
            duplicateMobileNumbers: `Mobile numbers already registered: ${duplicateNumbers.join(
              ", "
            )}`,
          });
          return;
        }
        if (totalAmount === 0) {
          setButtonDisabled(true);
          setErrors({
            duplicateMobileNumbers: `Please select a day`,
          });
          return;
        }

        // If payment is successful, save form data
        const response = await saveFormData();
        // Display the registration ID
        if (response.success) {
          navigate("/payment", {
            state: { responseData: response },
          });
          setRegistrationSuccess(true);
          // Reset form data
          setUsers([
            {
              name: "",
              phone: "",
              email: "",
              district: "",
              customDistrict: "",
              pincode: "",
              days: [],
            },
          ]);
          setTotalAmount(0);
        }

        // Clear errors
        setErrors({});
      } catch (error) {
        console.error("Error processing form:", error);
      }
    }
  };

  // Function to check duplicate mobile numbers
  const checkDuplicateMobileNumbers = async () => {
    try {
      const mobileNumbers = users.map((user) => user.phone);
      const response = await axios.post(
        `${BASE_URL}/api/checkDuplicateMobileNumbers`,
        { mobileNumbers }
      );

      if (response.data.success) {
        return response.data.duplicateMobileNumbers || [];
      } else {
        console.error(
          "Error checking duplicate mobile numbers:",
          response.data.error
        );
        return [];
      }
    } catch (error) {
      console.error("Error checking duplicate mobile numbers:", error);
      return [];
    }
  };

  const addUser = () => {
    setUsers((prevUsers) => [
      ...prevUsers,
      {
        name: "",
        phone: "",
        email: "",
        district: "",
        pincode: "",
        days: [],
      },
    ]);
  };

  return (
    <>
      <Header />
      <Banner />
      <section className="form-register-wrapper">
        <div className="container">
          <div className="form-wrapper">
            <div className="link-wrapper">
              If you are already registered.<Link to="/login">Click here</Link>{" "}
              to download your Ticket.
              <br />
              If you are facing any technical issues, <strong>
                WhatsApp
              </strong>{" "}
              chat with{" "}
              <a
                href="https://api.whatsapp.com/send?phone=9895277722"
                target="_blank"
              >
                <strong>9895277722</strong>
              </a>
            </div>
            <div className="note">
              <span className="nb">Note:-</span>Registration Fee{" "}
              <span className="price">RS 100 + 18% GST</span>, For 1 Day Pass.
            </div>
            <form className="registrationForm" onSubmit={handleSubmit}>
              {users.map((user, index) => (
                <div className="member-fields" key={index}>
                  <div className="row">
                    <div className="col-lg-6 form-group">
                      <input
                        placeholder="Name"
                        type="text"
                        name="name"
                        className={`form-control ${
                          errors[`name${index}`] ? "is-invalid" : ""
                        }`}
                        value={user.name}
                        onChange={(e) => handleChange(e, index)}
                      />
                      {errors[`name${index}`] && (
                        <div className="invalid-feedback">
                          {errors[`name${index}`]}
                        </div>
                      )}
                    </div>
                    <div className="col-lg-6 form-group">
                      <input
                        type="number"
                        placeholder="Contact Number"
                        name="phone"
                        maxLength={50}
                        className={`form-control ${
                          errors[`phone${index}`] ? "is-invalid" : ""
                        }`}
                        value={user.phone}
                        onChange={(e) => handleChange(e, index)}
                      />
                      {errors[`phone${index}`] && (
                        <div className="invalid-feedback">
                          {errors[`phone${index}`]}
                        </div>
                      )}
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-lg-6 form-group">
                      <input
                        type="email"
                        name="email"
                        className="form-control"
                        placeholder="Email Address"
                        value={user.email}
                        onChange={(e) => handleChange(e, index)}
                      />
                    </div>
                    <div className="col-lg-6 form-group">
                      <div className="mb-2">
                        <select
                          name="district"
                          className={`form-control ${
                            errors[`district${index}`] ? "is-invalid" : ""
                          }`}
                          value={user.district}
                          onChange={(e) => handleChange(e, index)}
                        >
                          <option>Select District</option>
                          {districts.map((district, districtIndex) => (
                            <option key={districtIndex} value={district}>
                              {district}
                            </option>
                          ))}
                          <option value="Other">Other</option>
                        </select>
                        {errors[`district${index}`] && (
                          <div className="invalid-feedback">
                            {errors[`district${index}`]}
                          </div>
                        )}
                      </div>
                      {user.district === "Other" && (
                        <div className="other_district">
                          <input
                            type="text"
                            className={`form-control ${
                              errors[`customDistrict${index}`]
                                ? "is-invalid"
                                : ""
                            }`}
                            id={`customDistrict${index}`}
                            name="customDistrict"
                            value={user.customDistrict}
                            onChange={(e) => handleChange(e, index)}
                            placeholder="Others specify"
                          />
                          {errors[`customDistrict${index}`] && (
                            <div className="invalid-feedback">
                              {errors[`customDistrict${index}`]}
                            </div>
                          )}
                        </div>
                      )}
                    </div>
                  </div>
                  <div className="form-group row">
                    <div className="col-lg-6">
                      <input
                        type="number"
                        className="form-control"
                        id={`pincode${index}`}
                        name="pincode"
                        placeholder="Pincode"
                        value={user.pincode}
                        onChange={(e) => handleChange(e, index)}
                      />
                    </div>
                    <div className="col-md-6">
                      <label>Select Ticket:</label>
                      <br />

                      <div className="checkbox-group">
                        <label
                          className="checkbox-container"
                          htmlFor={`day1${index}`}
                        >
                          Day 1
                          <input
                            type="checkbox"
                            id={`day1${index}`}
                            name="day1"
                            checked={user.days.includes("day1")}
                            onChange={(e) => handleCheckboxChange(e, index)}
                          />
                          <span className="checkmark"></span>
                        </label>

                        <label
                          className="checkbox-container"
                          htmlFor={`day2${index}`}
                        >
                          Day 2
                          <input
                            type="checkbox"
                            id={`day2${index}`}
                            name="day2"
                            checked={user.days.includes("day2")}
                            onChange={(e) => handleCheckboxChange(e, index)}
                          />
                          <span className="checkmark"></span>
                        </label>

                        <label
                          className="checkbox-container"
                          htmlFor={`day3${index}`}
                        >
                          Day 3
                          <input
                            type="checkbox"
                            id={`day3${index}`}
                            name="day3"
                            checked={user.days.includes("day3")}
                            onChange={(e) => handleCheckboxChange(e, index)}
                          />
                          <span className="checkmark"></span>
                        </label>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
              <div className="form-group row">
                <div className="col-md-12">
                  <div className="form-group">
                    <button type="button" onClick={addUser} id="addMemberBtn">
                      <span
                        className="glyphicon glyphicon-plus
"
                      ></span>
                      Add another Pass
                    </button>
                  </div>
                </div>
                <div className="col-md-12">
                  <div className="btn-wrapper">
                    <button
                      type="submit"
                      id="btn-submit"
                      disabled={isButtonDisabled}
                    >
                      Pay {totalAmount > 0 && totalAmount}
                    </button>
                  </div>
                </div>
              </div>
            </form>
            <div className="row">
              <div className="col-md-12">
                {registrationSuccess && (
                  <div className="mt-3 text-center alert alert-success">
                    <h4>Please complete the payment.</h4>
                  </div>
                )}
                {errors.duplicateMobileNumbers && (
                  <div className="alert alert-danger mt-3">
                    {errors.duplicateMobileNumbers}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </section>
      <Footer />
    </>
  );
};

export default Form;
