import React, { useState, useEffect } from "react";
import axios from "axios";
import { BASE_URL, axiosConfig } from "./constant";
import * as XLSX from "xlsx";
import UtcToIstConverter from "./componants/UtcToIstConverter";
const FailedUsers = () => {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [searchQuery, setSearchQuery] = useState("");
  const [totalCount, setTotalCount] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const usersPerPage = 30;
  useEffect(() => {
    fetchData();
  }, [searchQuery]);

  const fetchData = async () => {
    try {
      setLoading(true);
      const response = await axios.get(
        `${BASE_URL}/api/failed?name=${searchQuery}`
      );
      setUsers(response.data);
      setTotalCount(response.data && response.data.length);
    } catch (error) {
      setError(error);
    } finally {
      setLoading(false);
    }
  };

  const handleSearchChange = (event) => {
    setSearchQuery(event.target.value);
  };

  const updatePaymentStatus = async (id) => {
    const dataObject = {
      id: id,
      status: "success",
    };
    const dataArray = [dataObject];
    try {
      await axios.post(
        `${BASE_URL}/api/update_payment_status`,
        dataArray,
        axiosConfig
      );
      fetchData();
    } catch (error) {
      console.error("Error updating payment status:", error);
    }
  };

  const deleteUser = async (id) => {
    try {
      await axios.post(
        `${BASE_URL}/api/delete_user`,
        { userId: id },
        axiosConfig
      );
      fetchData();
    } catch (error) {
      console.error("Error updating payment status:", error);
    }
  };

  // Calculate pagination values
  const indexOfLastUser = currentPage * usersPerPage;
  const indexOfFirstUser = indexOfLastUser - usersPerPage;
  const currentUsers = users.slice(indexOfFirstUser, indexOfLastUser);

  // Change page
  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  const exportToExcel = () => {
    const data = users.map((user) => ({
      Name: user.name,
      Email: user.email,
      District: user.district,
      Phone: user.phone,
      Pincode: user.pincode,
      Days: user.days,
      Amount: user.paymentAmount,
      "Payment Status": user.paymentStatus,
    }));

    const ws = XLSX.utils.json_to_sheet(data);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, "User_Data_failed");
    XLSX.writeFile(wb, "User_Data_failed.xlsx");
  };
  if (loading) {
    return (
      <div className="loading-overlay">
        <div className="loading-spinner"></div>
      </div>
    );
  }

  if (error) {
    return (
      <section className="userDashboard">
        <div className="container-fluid">
          <div className="row">
            <div className="col-md-12">
              <h4>Error: {error.message}</h4>
            </div>
          </div>
        </div>
      </section>
    );
  }

  return (
    <>
      <section className="userDashboard">
        <div className="container-fluid">
          <div className="row">
            <div className="col-md-12">
              <h2 className="ova_title2 ">Registeration Offline Completion</h2>
            </div>
          </div>
          <div className="row">
            <div className="col-md-3">
              <div className="panel panel-default">
                <div className="panel-body">
                  <h4>Total Users</h4>
                  <h3>{totalCount}</h3>
                </div>
              </div>
            </div>
            <div className="col-md-6">
              <input
                type="text"
                className="form-control"
                placeholder="Search by name..."
                value={searchQuery}
                onChange={handleSearchChange}
              />
            </div>
            <div className="col-md-3">
              <div className="excel-wrap">
                <button onClick={exportToExcel} className="btn-submit">
                  Export to Excel
                </button>
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col-md-12">
              <div className="table-responsive">
                <table className="table table-bordered">
                  <thead>
                    <tr>
                      <th>No</th>
                      <th>Name</th>
                      <th>Email</th>
                      <th>District</th>
                      <th>Phone</th>
                      <th>Pincode</th>
                      <th>Days</th>
                      <th> Amount</th>
                      <th>Payment Status</th>
                      <th>Action</th>
                      <th>Date</th>
                    </tr>
                  </thead>
                  <tbody>
                    {currentUsers.map((user, index) => (
                      <tr key={user.id}>
                        <td>{index + 1}</td>
                        <td>{user.name}</td>
                        <td>{user.email}</td>
                        <td>{user.district}</td>
                        <td>{user.phone}</td>
                        <td>{user.pincode}</td>
                        <td>{user.days}</td>
                        <td>{user.paymentAmount}</td>

                        <td>
                          {user.paymentStatus}{" "}
                          <button
                            className="btn-small"
                            onClick={() => updatePaymentStatus(user.id)}
                          >
                            Convert
                          </button>
                        </td>
                        <td>
                          <button
                            className="btn-red"
                            onClick={() => deleteUser(user.id)}
                          >
                            Delete
                          </button>
                        </td>
                        <td>
                          <UtcToIstConverter
                            utcTimestamp={user.registration_date}
                          />
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
              <div className="paginate">
                {/* Bootstrap 3 Pagination */}
                <ul className="pagination pagination-lg">
                  <li className={currentPage === 1 ? "disabled" : ""}>
                    <button
                      className="btn btn-link"
                      onClick={() => paginate(currentPage - 1)}
                    >
                      &laquo; Prev
                    </button>
                  </li>
                  {Array.from({
                    length: Math.ceil(users.length / usersPerPage),
                  }).map((_, index) => (
                    <li
                      key={index}
                      className={currentPage === index + 1 ? "active" : ""}
                    >
                      <button
                        className="btn btn-link"
                        onClick={() => paginate(index + 1)}
                      >
                        {index + 1}
                      </button>
                    </li>
                  ))}
                  <li
                    className={
                      currentPage === Math.ceil(users.length / usersPerPage)
                        ? "disabled"
                        : ""
                    }
                  >
                    <button
                      className="btn btn-link"
                      onClick={() => paginate(currentPage + 1)}
                    >
                      Next &raquo;
                    </button>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default FailedUsers;
