import React, { useState, useEffect } from "react";
import axios from "axios";
import * as XLSX from "xlsx";
import { BASE_URL } from "./constant";
import DateConverter from "./componants/DateConverter";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
const Attendance = () => {
  const [users, setUsers] = useState([]);
  const [totalCount, setTotalCount] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const usersPerPage = 50;
  const [scannedDate, setScannedDate] = useState(new Date());
  const [loading, setLoading] = useState(false);

  const fetchData = async () => {
    try {
      setLoading(true);
      const formattedDate = scannedDate.toISOString().split("T")[0];

      const response = await axios.get(`${BASE_URL}/api/usersWithAttendance`, {
        params: { scannedDate: formattedDate },
      });

      setUsers(response.data);
      setTotalCount(response.data && response.data.length);
    } catch (error) {
      console.error("Error fetching data:", error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchData();
  }, [scannedDate]);

  // Calculate pagination values
  const indexOfLastUser = currentPage * usersPerPage;
  const indexOfFirstUser = indexOfLastUser - usersPerPage;
  const currentUsers = users.slice(indexOfFirstUser, indexOfLastUser);

  // Change page
  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  const exportToExcel = () => {
    const data = users.map((user) => ({
      Name: user.name,
      Phone: user.phone,
      scannedDate: user.scannedDate,
    }));

    const ws = XLSX.utils.json_to_sheet(data);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, "attendance_sheet");
    XLSX.writeFile(wb, "attendance_sheet.xlsx");
  };

  return (
    <>
      <section className="userDashboard">
        <div className="container-fluid">
          <div className="row">
            <div className="col-md-12">
              <h2 className="ova_title2 ">Attendance Sheet</h2>
            </div>
          </div>
          <div className="row">
            <div className="col-md-3">
              <div className="panel panel-default">
                <div className="panel-body">
                  <h4>Total Users</h4>
                  <h3>{totalCount}</h3>
                </div>
              </div>
            </div>
            <div className="col-md-6">
              <div className="date-wrapper">
                <p>Filter with Date</p>
                <DatePicker
                  selected={scannedDate}
                  onChange={(date) => setScannedDate(date)}
                  dateFormat="yyyy-MM-dd"
                />
              </div>
            </div>
            <div className="col-md-3">
              <div className="excel-wrap">
                <button onClick={exportToExcel} className="btn-submit">
                  Export to Excel
                </button>
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col-md-12">
              <div className="table-responsive">
                {loading ? (
                  <div className="loading-overlay">
                    <div className="loading-spinner"></div>
                  </div>
                ) : (
                  <table className="table table-bordered">
                    <thead>
                      <tr>
                        <th>No</th>
                        <th>Name</th>
                        <th>Phone</th>
                        <th>Program Date</th>
                      </tr>
                    </thead>
                    <tbody>
                      {currentUsers.map((user, index) => (
                        <tr key={user.id}>
                          <td>{index + 1}</td>
                          <td>{user.name}</td>
                          <td>{user.phone}</td>
                          <td>
                            <DateConverter utcTimestamp={user.scannedDate} />
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                )}
              </div>
              <div className="paginate">
                {/* Bootstrap 3 Pagination */}
                <ul className="pagination pagination-lg">
                  <li className={currentPage === 1 ? "disabled" : ""}>
                    <button
                      className="btn btn-link"
                      onClick={() => paginate(currentPage - 1)}
                    >
                      &laquo; Prev
                    </button>
                  </li>
                  {Array.from({
                    length: Math.ceil(users.length / usersPerPage),
                  }).map((_, index) => (
                    <li
                      key={index}
                      className={currentPage === index + 1 ? "active" : ""}
                    >
                      <button
                        className="btn btn-link"
                        onClick={() => paginate(index + 1)}
                      >
                        {index + 1}
                      </button>
                    </li>
                  ))}
                  <li
                    className={
                      currentPage === Math.ceil(users.length / usersPerPage)
                        ? "disabled"
                        : ""
                    }
                  >
                    <button
                      className="btn btn-link"
                      onClick={() => paginate(currentPage + 1)}
                    >
                      Next &raquo;
                    </button>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Attendance;
