const UtcToIstConverter = ({ utcTimestamp }) => {
  const convertToIst = (utcTimestamp) => {
    const date = new Date(utcTimestamp);

    const options = {
      timeZone: "Asia/Kolkata", // Indian Standard Time (IST)
      year: "numeric",
      month: "numeric",
      day: "numeric",
      hour: "numeric",
      minute: "numeric",
      hour12: true, // Use 12-hour format with AM and PM
    };

    // Convert the UTC timestamp to IST
    return date.toLocaleString("en-IN", options);
  };

  return <>{convertToIst(utcTimestamp)}</>;
};

export default UtcToIstConverter;
