import React from "react";
import Logo from "../images/OFAI-Logo1.png";
import svg from "../images/chevron-down.svg";
import { Link } from "react-router-dom";
function Header() {
  return (
    <>
      <header className="navigation">
        <div className="container">
          <div className="brand">
            <Link to="/">
              <img src={Logo} alt="OFAI" className="logo" />
            </Link>
          </div>
          <nav>
            <div className="nav-mobile">
              <a id="nav-toggle" href="#!">
                <span></span>
              </a>
            </div>
            <ul className="nav-list">
              <li>
                <Link to="/">Home</Link>
              </li>
              <li>
                <a href="https://www.ofai.org/organisation">Organisation </a>
                <img src={svg} className="svg" alt="" />
                <ul className="nav-dropdown">
                  <li>
                    <a href="https://www.ofai.org/mission-and-goals">
                      Mission and Goals
                    </a>
                  </li>
                  <li>
                    <a href="https://www.ofai.org/organisation/office-bearers">
                      OFAI Managing Committee
                    </a>
                  </li>
                  <li>
                    <a href="https://www.ofai.org/organisation/intercontinental-network-of-organic-farming-organisations-inofo">
                      Intercontinental Network of Organic Farming Organisations
                      (INOFO)
                    </a>
                  </li>
                </ul>
              </li>
              <li>
                <a href="https://www.ofai.org/affiliations">Affiliations</a>
              </li>
              <li>
                <a href="https://www.ofai.org/Resources">Resources </a>
                <img src={svg} className="svg" alt="" />
                <ul className="nav-dropdown">
                  <li>
                    <a href="https://www.ofai.org/faq-frequently-asked-questions">
                      FAQ
                    </a>
                  </li>
                </ul>
              </li>
              <li>
                <Link to="/login">Download Ticket</Link>
              </li>
              <li>
                <Link to="/">Register</Link>
              </li>
              <li>
                <a href="https://www.ofai.org/contact">Contact</a>
              </li>
            </ul>
          </nav>
        </div>
      </header>
    </>
  );
}

export default Header;
