import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";
import domtoimage from "dom-to-image";
import IDCard from "./IDCard";
import Header from "./componants/header";
import Footer from "./componants/footer";
import Banner from "./componants/Banner";
const Success = () => {
  const location = useLocation();
  const [users, setUsers] = useState([]);
  const responseData = location.state ? location.state.responseData : null;
  useEffect(() => {
    if (responseData && responseData.length > 0) {
      setUsers(responseData);
    }
  }, [responseData]);

  const downloadAllIDCards = async () => {
    const cards = document.querySelectorAll(".id-card");

    cards.forEach(async (card, index) => {
      const dataUrl = await domtoimage.toJpeg(card);

      // Replace 'USERNAME' with the actual username from your data
      const userid = users[index].name.replace(/\s/g, "_") || "noname";

      const a = document.createElement("a");
      a.href = dataUrl;
      a.download = `Ticket_${userid}.jpg`;
      a.click();
    });
  };

  return (
    <>
      <Header />
      <Banner />
      <section className="successPage">
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <div className="succssWrapper">
                <div className="mt-3 text-center alert alert-success">
                  <h4>
                    You are successfully registered for 8th National Organics
                    Farmers Convention.
                  </h4>
                </div>
                <button onClick={downloadAllIDCards} className="btn-submit">
                  Download Ticket
                </button>
                <div className="id-card-generator">
                  {users.map((item, index) => (
                    <div key={index} className="id-card">
                      <IDCard
                        name={item.name}
                        imageUrl={item.qrCodeUrl}
                        days={item.days.length}
                      />
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <Footer />
    </>
  );
};

export default Success;
