import React, { useState, useEffect } from "react";
import axios from "axios";
import { BASE_URL } from "./constant";
import * as XLSX from "xlsx";
import UtcToIstConverter from "./componants/UtcToIstConverter";
const AdminDashboard = () => {
  const [users, setUsers] = useState([]);
  const [totalCount, setTotalCount] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const usersPerPage = 50;

  useEffect(() => {
    axios
      .get(`${BASE_URL}/api/users`)
      .then((response) => {
        setUsers(response.data);
        setTotalCount(response.data && response.data.length);
      })
      .catch((error) => console.error("Error fetching data:", error));
  }, []);

  // Calculate pagination values
  const indexOfLastUser = currentPage * usersPerPage;
  const indexOfFirstUser = indexOfLastUser - usersPerPage;
  const currentUsers = users.slice(indexOfFirstUser, indexOfLastUser);

  // Change page
  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  const exportToExcel = () => {
    const data = users.map((user) => ({
      Name: user.name,
      Email: user.email,
      District: user.district,
      Phone: user.phone,
      Pincode: user.pincode,
      Days: user.days,
      Amount: user.paymentAmount,
      "Payment Status": user.paymentStatus,
    }));

    const ws = XLSX.utils.json_to_sheet(data);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, "User_Data_Success");
    XLSX.writeFile(wb, "User_Data_Success.xlsx");
  };

  return (
    <>
      <section className="userDashboard">
        <div className="container-fluid">
          <div className="row">
            <div className="col-md-12">
              <h2 className="ova_title2 ">Registered Users</h2>
            </div>
          </div>
          <div className="row">
            <div className="col-md-3">
              <div className="panel panel-default">
                <div className="panel-body">
                  <h4>Total Users</h4>
                  <h3>{totalCount}</h3>
                </div>
              </div>
            </div>
            <div className="col-md-9">
              <div className="excel-wrap">
                <button onClick={exportToExcel} className="btn-submit">
                  Export to Excel
                </button>
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col-md-12">
              <div className="table-responsive">
                <table className="table table-bordered">
                  <thead>
                    <tr>
                      <th>No</th>
                      <th>Name</th>
                      <th>Email</th>
                      <th>District</th>
                      <th>Phone</th>
                      <th>Pincode</th>
                      <th>Days</th>
                      <th> Amount</th>
                      <th>Payment Status</th>
                      <th>Registered at</th>
                    </tr>
                  </thead>
                  <tbody>
                    {currentUsers.map((user, index) => (
                      <tr key={user.id}>
                        <td>{index + 1}</td>
                        <td>{user.name}</td>
                        <td>{user.email}</td>
                        <td>{user.district}</td>
                        <td>{user.phone}</td>
                        <td>{user.pincode}</td>
                        <td>{user.days}</td>
                        <td>{user.paymentAmount}</td>
                        <td>{user.paymentStatus}</td>
                        <td>
                          <UtcToIstConverter
                            utcTimestamp={user.registration_date}
                          />
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
              <div className="paginate">
                {/* Bootstrap 3 Pagination */}
                <ul className="pagination pagination-lg">
                  <li className={currentPage === 1 ? "disabled" : ""}>
                    <button
                      className="btn btn-link"
                      onClick={() => paginate(currentPage - 1)}
                    >
                      &laquo; Prev
                    </button>
                  </li>
                  {Array.from({
                    length: Math.ceil(users.length / usersPerPage),
                  }).map((_, index) => (
                    <li
                      key={index}
                      className={currentPage === index + 1 ? "active" : ""}
                    >
                      <button
                        className="btn btn-link"
                        onClick={() => paginate(index + 1)}
                      >
                        {index + 1}
                      </button>
                    </li>
                  ))}
                  <li
                    className={
                      currentPage === Math.ceil(users.length / usersPerPage)
                        ? "disabled"
                        : ""
                    }
                  >
                    <button
                      className="btn btn-link"
                      onClick={() => paginate(currentPage + 1)}
                    >
                      Next &raquo;
                    </button>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default AdminDashboard;
